.cancel {
  display: block;
  background-color: transparent;
  box-sizing: border-box;
  width: 36px;
  height: 36px;
  outline: none;
  border: none;
  padding: 8px;
  margin: 0;
  cursor: pointer;
}

.cancel:active {
  opacity: 0.5;
}

.cancel:disabled {
  cursor: default;
  opacity: 1;
}
